import { baseStatements } from '../index';
import { usersConstants } from '../../constants';

export const loginReducer = (state = baseStatements.initialState, action) => {
	switch (action.type) {
		case usersConstants.LOGIN_REQUEST:
			return {
				...state,
				request: action.request,
				...baseStatements.request()
			};
		case usersConstants.LOGIN_SUCCESS:
			return {
				...state,
				data: action.data,
				...baseStatements.success()
			};
		case usersConstants.LOGIN_ERROR:
			return {
				...state,
				...baseStatements.error(),
				data: action.data
			};
		default:
			return state;
	}
};
